@import './assets/constants.scss';

@font-face {
  font-family: 'Neutral Face';
  src:
    url('./assets/fonts/neutralface-bold-webfont.woff') format('woff'),
    url('./assets/fonts/neutralface-bold-webfont.woff2') format('woff2'),
    url('./assets/fonts/NeutralFace-Bold.otf') format('opentype');
  font-weight: bold;
}

html {
    font-size: 20px;
}

body {
    width: 100%;
    height: 100%;
    font-family: $general, sans-serif;
    margin: 0;
}

html,
#root {
    // background-color: $gray;
    width: 100%;
    height: 100%;
}

#root {
    margin: auto;
    max-width: 410px;
    height: 100%;
}

.launch-screen {
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    .launch-screen-logo-center {
        height: 51px;
        position: absolute;
        z-index: 4;
        animation: center 2s ease-in-out 1;
    }
    .launch-screen-logo-left {
        width: 70px;
        margin: auto;
        position: absolute;
        top: -58px;
        bottom: 0;
        left: -67.2px;
        z-index: 2;
    }
    .launch-screen-logo-right{
        width: 70px;
        margin: auto;
        position: absolute;
        top: 0;
        right: -65px;
        bottom: -55px;
        z-index: 2;
    }
    .vv {
        top: 0;
    }
    .ss {
        bottom: 0;
    }
}

.bgg {
    z-index: 1;
    height: 100%;
    width: 100%;
}

@keyframes center {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

// .home {
