.header {
    // color: rgb(222, 228, 91);
    // color: rgb(245, 231, 76);
    // color: rgb(254, 193, 74);
    // color: rgb(248, 154, 83);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 .25em;
    position: sticky;
    top: 0;
    z-index: 10;
    background-color: $white; //$gray;
    h1 {
        display: flex;
        flex-direction: column;
        text-transform: uppercase;
        font-weight: normal;
        font-family: 'Neutral Face','Inter',sans-serif;
        font-size: 1.1rem;
        margin-right: 10px;
        span {
            line-height: 22px;
        }
        img {
            margin: 0 4px;
            height: 17px;
        }
    }
    .favs {
        height: 25px;
        display: none;
    }
}

a {
    text-decoration: none;
    color: black;
}


.discount-item {
    display: grid;
}

.flat-divs {
    display: grid;
    gap: 4px;
    grid-template-columns: repeat(4, 1fr);
}

.flat-div {
    width: auto;
    display: flex;
    flex-direction: column;
    button {
        height: 20px;
        margin: 10px 0.25em;
    }
}

.flat-title {
    font-size: .45rem;
    font-family: 'Neutral Face', 'Inter', sans-serif;
    font-weight: bold;
    text-transform: uppercase;
}

*{
    outline: none;
}

.line {
    width: 30px;
    margin: 0 10px;
    position: relative;
    top: 3px;
    display: inline !important;
}

.launch-screen {
    display: flex;
    justify-content: center;
    h1 {
        display: flex;
        flex-direction: column;
        text-transform: uppercase;
        font-weight: normal;
        font-family: 'Neutral Face','Inter',sans-serif;
        font-size: 1.1rem;
        margin-right: 10px;
        span {
            line-height: 22px;
        }
        img {
            margin: 0 4px;
            height: 17px;
        }
    }
}

.signup-pages-slider {
    height: 100%;
    .slick-list {
        height: 100%;
        .slick-track {
            height: 100%;
            div {
                height: 100%; //auto; //
                .signup-slider-page {
                    height: calc(100% - 1.5em*2);
                    position: relative;
                    padding: 1.5em 0;
                    .signup-slider-page_label {
                        font-size: .8rem;
                        margin: 1em;
                        opacity: .7;
                        line-height: 24px;
                    }
                    .signup-slider-page_button {
                        position: absolute;
                        bottom: 35px; //79px;
                        right: 10px;
                        &--last {
                            right: 0;
                            left: 0;
                            width: 86%;
                            text-transform: capitalize;
                            bottom: 48px;
                            margin: auto !important;
                            padding: 10px !important
                        }
                    }
                }
            }
        }
    }
    .slick-dots li button:before {
        font-size: 10px;
    }
    .slick-dots {
        bottom: 214px;
        li {
            margin: 0;
        }
    }
}

.logo-with-text {
    width: 160px;
    padding: 0 .5em .5em;
}

.signup-slider-page_buttons-wrapper {
    height: auto !important;
    display: flex;
    justify-content: flex-end;
}
.screen-wrapper {
    overflow: hidden;
    background-repeat: no-repeat;
    position: relative;
    height: 500px !important;
    &--first {
        background-image: url(./assets/images/badge-4.svg);
        background-size: 109%;
        background-position: -98px -4px;
        img {
            width: 127px;
            border-radius: 1000px;
            position: absolute;
            opacity: 0;
            // animation: image 1.5s ease-in-out 1;
        }
        .a {
            top: 117px;
            left: 72px;
        }
        .b {
            bottom: 21px;
            left: 59px;
        }
        .c {
            right: 45px;
            top: 34px;
        }
        .d {
            bottom: 48px;
            right: -20px;
        }
    }
    &--middle {
        position: relative;
        background-image: url(./assets/images/badge-1.svg);
        background-size: 225%;
        background-position: -24px 48px;
        .aa {
            position: absolute;
            background-color: $gray;
            width: 400px;
            padding: 2px 0;
            opacity: 0;
            // animation: image 1.5s ease-in-out 1;
            &:first-of-type {
                top: 37px;
                right: -65px;
                transform: rotate(7deg);
            }
            &:last-of-type {
                bottom: 114px;
                transform: rotate(-17deg);
            }
        }
    }
    &--last {
        position: relative;
        background-image: url(./assets/images/badge-3.svg);
        background-size: 111%;
        background-position: 66px 56px;
    }
}

@keyframes image {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
